// require.ensure 是 Webpack 的特殊语法，用来设置 code-split point
const Home = resolve => {
    require.ensure(['./views/index.vue'], () => {
        resolve(require('./views/index.vue'));
    });
};

const routers = [{
    path: '/',
    name: '_home',
    component: Home
},{
    path: '/topic/:id',
    name: 'topic',
    component(resolve) {
        require.ensure(['./views/topic.vue'], () => {
            resolve(require('./views/topic.vue'));
        });
    }
}, {
    path: '/user',
    name: 'user',
    component(resolve) {
        require.ensure(['./views/user.vue'], () => {
            resolve(require('./views/user.vue'));
        });
    }
}, {
    path: '/home',
    name: 'home',
    component(resolve) {
        require.ensure(['./views/class.vue'], () => {
            resolve(require('./views/class.vue'));
        });
    }
}, {
    path: '/class',
    name: 'class',
    component(resolve) {
        require.ensure(['./views/school.vue'], () => {
            resolve(require('./views/school.vue'));
        });
    }
}, {
    path: '/store',
    name: 'store',
    component(resolve) {
        require.ensure(['./views/store.vue'], () => {
            resolve(require('./views/store.vue'));
        });
    }
}, {
    path: '/myClass',
    name: 'myClass',
    component(resolve) {
        require.ensure(['./views/myClass.vue'], () => {
            resolve(require('./views/myClass.vue'));
        });
    }
}, {
    path: '/classDetail/:id',
    name: 'classDetail',
    component(resolve) {
        require.ensure(['./views/classDetail.vue'], () => {
            resolve(require('./views/classDetail.vue'));
        });
    }
}, {
    path: '/classDetail/teacher/:id',
    name: 'classDetail2teacher',
    component(resolve) {
        require.ensure(['./views/teacher.vue'], () => {
            resolve(require('./views/teacher.vue'));
        });
    }
},{
    path: '/teacher/:id',
    name: 'teacher',
    component(resolve) {
        require.ensure(['./views/teacher.vue'], () => {
            resolve(require('./views/teacher.vue'));
        });
    }
}, {
    path: '/goods/:id',
    name: 'goods',
    component(resolve) {
        require.ensure(['./views/goods.vue'], () => {
            resolve(require('./views/goods.vue'));
        });
    }
}, {
    path: '/login',
    name: 'login',
    component(resolve) {
        require.ensure(['./views/login.vue'], () => {
            resolve(require('./views/login.vue'));
        });
    }
}, {
    path: '/signUp',
    name: 'signUp',
    component(resolve) {
        require.ensure(['./views/signUp.vue'], () => {
            resolve(require('./views/signUp.vue'));
        });
    }
},{
    path: '*',
    component(resolve) {
        require.ensure(['./views/class.vue'], () => {
            resolve(require('./views/class.vue'));
        });
    }
}];

export default routers;



