import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);

const userStore = new Vuex.Store({
    state: {
        address: {
           province: '北京',
           city: '北京'
        }
    },
    getters: {//getters 是 store 的计算属性
        getAddress(state) {
            return state.address;
        }
    },
    mutations: {//mutations 是 直接变更状态
        setAddress(state, obj) {
            state.address = obj;
        }
    },
    actions: {//提交的是 mutation
        setAddress({ commit }, user) {
            commit('setAddress', user);
        }
    }
});

export default userStore;



